
html, body {
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    //align-items: stretch;
    width: 100vw;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}



.photo-capture{
    position: relative;

    .frame {
        position: absolute;
        top: 40px;
        left: 20px;
        z-index: 4;
    }


    video {
        display: block;
        width: 100%;
        max-width: 400px;
    }
   
   .output {
       display: block;
       max-width: 100%;
   }

}
.images {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    img {
        flex: 0 0 33%;
        width: 33%;
        display: block;
        object-fit: cover;
    }
}


.actions {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    width: 100%;
}

button {
    background-color:teal;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    min-width: 400px;
  }


.view-points {
    width: 80%;
    height: 400px;
}

g.selected {
    path {
        fill: teal;
    }
}